body {
  background-color: #f8f8f8;
}

#wrapper {
  width: 100%;
}

#page-wrapper {
  padding: 0 15px;
  min-height: 568px;
  background-color: #fff;
}

@media(min-width:768px) {
  #page-wrapper {
    position: inherit;
    margin: 0 0 0 250px;
    padding: 0 30px;
    border-left: 1px solid #e7e7e7;
  }
}

.btn-group.in-header{
  margin-top:0px;
  margin-bottom:0px;
}

  .btn-outline {
    color: inherit;
    background-color: transparent;
    transition: all .5s;
  }

  .btn-primary.btn-outline {
    color: #428bca;
  }

  .btn-success.btn-outline {
    color: #5cb85c;
  }

  .btn-info.btn-outline {
    color: #5bc0de;
  }

  .btn-warning.btn-outline {
    color: #f0ad4e;
  }

  .btn-danger.btn-outline {
    color: #d9534f;
  }

  .btn-primary.btn-outline:hover,
  .btn-success.btn-outline:hover,
  .btn-info.btn-outline:hover,
  .btn-warning.btn-outline:hover,
  .btn-danger.btn-outline:hover {
    color: #fff;
  }

.btn:not(.btn-raised):not(.btn-link):hover,
.btn:not(.btn-raised):not(.btn-link):focus,
.input-group-btn .btn:not(.btn-raised):not(.btn-link):hover,
.input-group-btn .btn:not(.btn-raised):not(.btn-link):focus {
  opacity: 0.4;
}


.chat {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .chat li {
    margin-bottom: 10px;
    padding-bottom: 5px;
    border-bottom: 1px dotted #999;
  }

  .chat li.left .chat-body {
    margin-left: 60px;
  }

  .chat li.right .chat-body {
    margin-right: 60px;
  }

  .chat li .chat-body p {
    margin: 0;
  }

  .panel .slidedown .glyphicon,
  .chat .glyphicon {
    margin-right: 5px;
  }

  .chat-panel .panel-body {
    height: 350px;
    overflow-y: scroll;
  }

  .login-panel {
    margin-top: 25%;
  }

  .login-logo{
    max-height: 90px;
    padding: 5px;
  }

@media screen and (max-width: 767px){
  .login-container{
    margin: 15px auto 140px;
  }
}
@media screen and (min-width: 768px){
  .login-container{
    margin: 120px auto 140px;
  }
}

.logo-heading{
  text-align:center;
}

  .auth-form-group{
    margin-top:10px;
  }

  .flot-chart {
    display: block;
    height: 400px;
  }

  .flot-chart-content {
    width: 100%;
    height: 100%;
  }

  .dataTables_wrapper {
    position: relative;
    clear: both;
  }

  table.dataTable thead .sorting,
  table.dataTable thead .sorting_asc,
  table.dataTable thead .sorting_desc,
  table.dataTable thead .sorting_asc_disabled,
  table.dataTable thead .sorting_desc_disabled {
    background: 0 0;
  }

  table.dataTable thead .sorting_asc:after {
    content: "\f0de";
    float: right;
    font-family: fontawesome;
  }

  table.dataTable thead .sorting_desc:after {
    content: "\f0dd";
    float: right;
    font-family: fontawesome;
  }

  table.dataTable thead .sorting:after {
    content: "\f0dc";
    float: right;
    font-family: fontawesome;
    color: rgba(50,50,50,.5);
  }

  .btn-circle {
    width: 30px;
    height: 30px;
    padding: 6px 0;
    border-radius: 15px;
    text-align: center;
    font-size: 12px;
    line-height: 1.428571429;
  }

  .btn-circle.btn-lg {
    width: 50px;
    height: 50px;
    padding: 10px 16px;
    border-radius: 25px;
    font-size: 18px;
    line-height: 1.33;
  }

  .btn-circle.btn-xl {
    width: 70px;
    height: 70px;
    padding: 10px 16px;
    border-radius: 35px;
    font-size: 24px;
    line-height: 1.33;
  }

  .show-grid [class^=col-] {
    padding-top: 10px;
    padding-bottom: 10px;
    border: 1px solid #ddd;
    background-color: #eee!important;
  }

  .show-grid {
    margin: 15px 0;
  }

  .huge {
    font-size: 40px;
  }

  .panel-green {
    border-color: #5cb85c;
  }

  .panel-green .panel-heading {
    border-color: #5cb85c;
    color: #fff;
    background-color: #5cb85c;
  }

  .panel-green a {
    color: #5cb85c;
  }

  .panel-green a:hover {
    color: #3d8b3d;
  }

  .panel-red {
    border-color: #d9534f;
  }

  .panel-red .panel-heading {
    border-color: #d9534f;
    color: #fff;
    background-color: #d9534f;
  }

  .panel-red a {
    color: #d9534f;
  }

  .panel-red a:hover {
    color: #b52b27;
  }

  .panel-yellow {
    border-color: #f0ad4e;
  }

  .panel-yellow .panel-heading {
    border-color: #f0ad4e;
    color: #fff;
    background-color: #f0ad4e;
  }

  .panel-yellow a {
    color: #f0ad4e;
  }

  .panel-yellow a:hover {
    color: #df8a13;
  }
